import { useRecoilValue } from 'recoil';
import { useEffect } from 'react';
import { authAtom } from '_state';
import useMetaMask from 'utils/useMetaMask';
import './Home.css';
import { useCallback, useState } from 'react';
import { abi } from 'utils/abi';
import { useWeb3React } from "@web3-react/core";
import { useRef } from 'react';
export { Home };

function Home() {
	const ALIN_ADDRESS = "0xF24370313CE704C36A8cc8A0DAEd93e53A58B609";
	const auth = useRecoilValue(authAtom);
	const [results, setResults] = useState([]);
	const [toAddress, setToAddress] = useState('');
	const [amount, setAmount] = useState('')
	const { connect, disconnect, isActive, account } = useMetaMask();
	const { library } = useWeb3React();
	const [alinToken, setAlinToken] = useState();
	const [countDown, setTimeCountDown] = useState(86400);
	const [buttonText, setButtonText] = useState("Start Countdown")
	const [countDownState, setCountDownState] = useState(false); 
	console.log(library);
	// var web3 = library;

	useEffect(() => {
		fetchData();
	}, []);
	useEffect(() => {
		if (library) {
			setAlinToken(new library.eth.Contract(abi, ALIN_ADDRESS));
		}
	}, [library])

	const onChangeAddress = useCallback((event) => {
		setToAddress(event.target.value);
	});

	const onChangeAmount = useCallback((event) => {
		setAmount(event.target.value);
	})

	const fetchData = () => {
		fetch("https://api-testnet.bscscan.com/api?module=account&action=tokentx&contractaddress=0xF24370313CE704C36A8cc8A0DAEd93e53A58B609&page=1&offset=100&sort=asc&apikey=YourApiKeyToken")
			.then((response) => response.json())
			.then((data) => {
				var results = data.result;
				setResults(results);
			});
	}

	var balanceOfAddress;
	const transfer = (event) => {

		alinToken.methods.balanceOf(account).call(function (err, res) {
			if (err) {
				console.log("An error occured", err)
				return
			}
			balanceOfAddress = res / 1000000000000000000;
			console.log("The balance is: ", balanceOfAddress);
			console.log(account);
			alinToken.methods
				.transfer(toAddress, library.utils.toWei(amount))
				.send({
					from: account
				}, function (err, res) {
					if (err) {
						console.log(toAddress);
						console.log(account)
						console.log("An error occured", err)
						return
					}
					console.log("Hash of the transaction: " + res)
					this.fetchData()
				})
		})
	}

	const increment = useRef(null);
	const unfreeze = (event) => {
			

			if (!countDownState) {
				setCountDownState(true);
				setButtonText("Stop Countdown");

				var cnt = countDown;
				increment.current = setInterval(() => {
					cnt --;
					setTimeCountDown(cnt);
					console.log(cnt);
					if (cnt <= 0) {
						alinToken.methods
						.unfreeze()
						.send({
							from: account
						}, function (err, res) {
							if (err) {
								console.log(account)
								console.log("An error occured", err)
								return
							}
							console.log("Hash of the transaction: " + res)
						})
						cnt = countDown;
					}
				}, 1000);			
			} else {
				console.log(increment.current);
				// alert();
				setCountDownState(false);
				setButtonText("Start Countdown");
				setTimeCountDown(86400);
				 clearInterval(increment.current);

			}

			
	}

	const rewards = (event) => {
		console.log("Gifts to Holders")
	}

	return (

		<div className="limiter">
			<div className="container-table100">
				<div className="wrap-table100 p-4">
					<h1>Hi, {auth?.username}!</h1>
					<div className="text-center p-4">
						<button
							className="btn btn-primary"
							onClick={connect}>{isActive ? (account) : 'Connect Wallet'}
						</button>
						{isActive ?
							<div className="transfer" style={{ paddingTop: "20px" }}>
								<label style={{ verticalAlign: "middle" }}>To: </label>
								<input className='common-input' type="text" style={{ marginRight: "20px" }} value={toAddress} onChange={onChangeAddress} />
								<label style={{ verticalAlign: "middle" }}>Amount: </label>
								<input className='common-input' type="text" style={{ marginRight: "20px" }} value={amount} onChange={onChangeAmount} />
								<button className='btn btn-primary' onClick={transfer} style={{ verticalAlign: "top", marginRight: "20px" }}>Transfer</button>
								<button className='btn btn-primary' onClick={unfreeze} style={{ verticalAlign: "top", marginRight: "20px" }}>{buttonText}</button>
								{/* <button className='btn btn-primary' onClick={rewards} style={{verticalAlign: "top"}}>Gift to Holders</button> */}
							</div>
							: ''}
					</div>
					<div className="table100 ver1 m-b-110">
						<div className="table100-head">
							<table>
								<thead>
									<tr className="row100 head">
										<th className="cell100 column1">No</th>
										<th className="cell100 column2">Symbol</th>
										<th className="cell100 column3">To</th>
										<th className="cell100 column4">From</th>
										<th className="cell100 column5">Amount</th>
									</tr>
								</thead>
							</table>
						</div>

						<div className="table100-body js-pscroll">
							<table>
								<tbody>
									{
										results.map((item, keys) => {
											return (
												<tr className="row100 body" key={keys}>
													<td className="cell100 column1">{keys + 1}</td>
													<td className="cell100 column2">{item.tokenName}</td>
													<td className="cell100 column3">{item.to}</td>
													<td className="cell100 column4">{item.from}</td>
													<td className="cell100 column5">{Math.round(item.value / 1000000000000000000)}</td>
												</tr>
											);
										}
									)}
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
