import { useState } from 'react'
import tokenLogo from 'assets/token-logo.png'
import bnbLogo from 'assets/bnb-logo.png'

export { BuyForm }

function BuyForm({buyTokens}) {
    const [output, setOutput] = useState(0);

    const onChange = (e) => {
        const tokenAmount = e.currentTarget.value.toString();
        setOutput(tokenAmount * 400);
        buyTokens(tokenAmount, output);
        // console.log(output);
    }

    return (
            <div>
                <div>
                    <label className="float-left"><b>Input</b></label>
                    {/* <span className="float-right text-muted">
                        Balance: 
                    </span> */}
                </div>
                <div className="input-group mb-4">
                    <input
                        onChange={onChange}
                        type="text"
                        className="form-control form-control-lg"
                        placeholder="0"
                        required />
                    <div className="input-group-append">
                        <div className="input-group-text">
                            <img src={bnbLogo} height='32' alt="" />
                            &nbsp;&nbsp;&nbsp; BNB
                        </div>
                    </div>
                </div>
                <div>
                    <label className="float-left"><b>Output</b></label>
                    <span className="float-right text-muted">
                    </span>
                </div>
                <div className="input-group mb-2">
                    <input
                        type="text"
                        className="form-control form-control-lg"
                        placeholder="0"
                        value={output}
                        disabled
                    />
                    <div className="input-group-append">
                        <div className="input-group-text">
                            <img src={tokenLogo} height='32' alt="" />
                            &nbsp; ALIN
                        </div>
                    </div>
                </div>
                <div className="mb-5">
                    <span className="float-left text-muted">Exchange Rate</span>
                    <span className="float-right text-muted">1 BNB = 400 ALIN</span>
                </div>                                       
            </div>

    );
}