import { useRecoilValue } from 'recoil';
import { useEffect } from 'react';
import { authAtom } from '_state';
import useMetaMask from 'utils/useMetaMask';
import { useState } from 'react';
import { abi } from 'utils/abi';
import { useWeb3React } from "@web3-react/core";
import { BuyForm } from '_components';
import { SellForm } from '_components';

export { Swap };


function Swap() {
    const ALIN_ADDRESS = "0xF24370313CE704C36A8cc8A0DAEd93e53A58B609";
    const auth = useRecoilValue(authAtom);
    const { connect, disconnect, isActive, account } = useMetaMask();
    const { library } = useWeb3React();
    const [alinToken, setAlinToken] = useState();
    const [currentForm, setCurrentForm] = useState('buy');
    const [etherAmount, setEtherAmount] = useState(0);
    const [tokenAmount, setTokenAmount] = useState(0);
    console.log(library);

    const onSubmit = (e) => {
        if (currentForm == 'buy') {
            console.log(etherAmount);

            alinToken.methods.buy().send({ value: library.utils.toWei(etherAmount), from: account }).then(() => {
                
            });
        } else {
            console.log(tokenAmount)
            alinToken.methods.sell(tokenAmount).send({ from: account }).then(() => {

            })
        }
    }

    const buyTokens = (etherAmount, tokenAmount) => {
        setEtherAmount(etherAmount);
        setTokenAmount(tokenAmount)
    }

    const sellTokens = (tokenAmount, etherAmount) => {
        setTokenAmount(tokenAmount);
        setEtherAmount(etherAmount);
    }

    useEffect(() => {
        if (library) {
            setAlinToken(new library.eth.Contract(abi, ALIN_ADDRESS))
        }
    }, [library])

    const [content, setContent] = useState(<BuyForm buyTokens={buyTokens} />)

    console.log(alinToken)

    const onBuy = (e) => {
        setCurrentForm('buy');
        setContent(<BuyForm buyTokens={buyTokens} />)
    }

    const onSell = (e) => {
        setCurrentForm('sell');
        setContent(<SellForm sellTokens={sellTokens} />)
    }

    return (
        <div className="limiter">
            <div className="container-table100">
                <div className="wrap-table100 p-4">
                    <h1>Hi, {auth?.username}!</h1>
                    <div className="text-center p-4">
                        <button
                            className="btn btn-primary"
                            onClick={connect}>{isActive ? (account) : 'Connect Wallet'}
                        </button>
                    </div>
                    <div className="text-center p-4">
                        <div className="d-flex justify-content-between mb-3">
                            <button
                                className="btn btn-light"
                                onClick={onBuy}>
                                Buy
                            </button>
                            <span className="text-muted">&lt; &nbsp; &gt;</span>
                            <button
                                className="btn btn-light"
                                onClick={onSell}
                            >
                                Sell
                            </button>
                        </div>
                        {/* <form className="mb-3" onSubmit={onSubmit}> */}
                            {content}
                            <button className="btn btn-primary btn-block btn-lg" onClick={onSubmit}>SWAP!</button>
                        {/* </form> */}
                    </div>
                </div>
            </div>
        </div>
    );
}
